<template>
  <v-container>
    
    <v-row>
      <v-col md="8" offset-md="2">
        <v-text-field label="Search" v-model="food" append-icon="mdi-magnify" clearable @keyup="searchCalories"></v-text-field>

      </v-col>

    </v-row>
    <h1>{{food}}</h1>
    <v-data-table dense :headers="headers" :items="foods" item-key="name" class="elevation-1">
      <template v-slot:[`item.item.country_code`] ="{item}">
        <div class="p-2">
          <img :src="`https://www.countryflags.io/${item.item.country_code}/flat/64.png`" class="countryImage">
        </div>
      </template>

    </v-data-table>

  </v-container>

</template>

<script>
export default {
  name: "Home",

  data() {
    return {
      food: "",

      headers: [
        {
          text: "Food",
          align: "start",
          sortable: false,
          value: "item.brand_name",
        },
        { text: "Description", value: "item.description" },
        { text: "Country", value: "item.country_code" },
        { text: "Calories", value: "item.nutritional_contents.energy.value" },
        { text: "Fat (g)", value: "item.nutritional_contents.fat" },
        { text: "Carbs (g)", value: "item.nutritional_contents.carbohydrates" },
        { text: "Protein (g)", value: "item.nutritional_contents.protein" },
        { text: "Iron (%)", value: "item.nutritional_contents.iron" },
      ],

      foods: [],
    };
  },

  methods: {
    async searchCalories() {
      await this.$store.dispatch("getCalories", this.food);
      this.foods = this.$store.getters.GET_CALORIES;
    },
  },

  async mounted() {
    await this.$store.dispatch("getCalories", "Couscous");
    this.foods = this.$store.getters.GET_CALORIES;
  },
};
</script>


<style>
.countryImage{
  border-radius: 50px;
}
</style>
