import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import config from "./config";



Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    Calories : [],
  },
  mutations: {
    SET_CALORIES(state , payload){
      state.Calories = payload;
    }

  },
  actions: {
    

    async getCalories({commit} , food){
      await axios.get(config.apiUrl+food).then((response) => {
        commit("SET_CALORIES" , response.data);
      })

    }

  },
  getters: {
    GET_CALORIES(state){
      return state.Calories;
    }
  },
});
